body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-exo-2: "Exo 2";
  --font-concert-one: "Concert One";
  --font-bebas-neue: "Bebas Neue";
  --font-inherit: inherit;
  --font-intel-clear-pro: "Intel Clear Pro";
  --font-futura-pt: "Futura PT";
  --font-anton: Anton;
  --font-cabin: Cabin;

  /* font sizes */
  --font-size-8xl: 27px;
  --font-size-21xl: 40px;
  --font-size-7xl: 26px;
  --font-size-17xl: 36px;
  --font-size-xl: 20px;
  --font-size-10xl: 29px;

  /* Colors */
  --color-gray-100: #120a14;
  --color-gray-200: rgba(0, 0, 0, 0.1);
  --color-white: #fff;
  --color-crimson: #ff4262;

  /* Gaps */
  --gap-lg: 18px;

  /* Paddings */
  --padding-xl: 20px;
  --padding-5xl: 24px;
  --padding-16xl: 35px;

  /* border radiuses */
  --br-xl: 20px;
  --br-3xs: 10px;
}
